.section-cases__position {
  background-color: #ffffff;
  padding: 56px 20px 40px;
  @media screen and (min-width: 768px) {
    padding: 48px 20px 40px;
  }
  @media screen and (min-width: 1360px) {
    padding: 80px 0px 80px;
  }
};

.section-cases {
  font-family: 'Open Sans';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 22px;
text-align: center;
color: #333333;
@media screen and (min-width: 768px) {
  font-size: 18px;
line-height: 25px;
}
@media screen and (min-width: 1360px) {
  font-size: 18px;
line-height: 33px;
}

};

.section-cases__title {
  font-family: 'Open Sans';
font-style: normal;
font-weight: 400;
font-size: 32px;
line-height: 44px;
text-align: center;
margin: 16px 30px 0px;
color: #333333;
@media screen and (min-width: 768px) {
  font-size: 40px;
line-height: 54px;
}
@media screen and (min-width: 1360px) {
  font-size: 40px;
line-height: 48px;
}
};

.section-cases__text {
  font-family: 'Open Sans';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 22px;
text-align: center;
margin: 24px 36px 31px;
color: #333333;
@media screen and (min-width: 768px) {
  margin: 24px 36px 23px;
  font-size: 18px;
line-height: 25px;
}
}

.grid{
  list-style: none;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -8px;
  margin-right: -8px;
  margin-bottom: -16px;
  margin-top: 0;
  @media screen and (min-width: 768px) {
    display: flex;
    padding: 0;
  }
}

.grid-item{
  flex-basis: calc(100% / 3 - 32px);
  min-width: 280px;
  margin-right: 9px;
  margin-left: 9px;
  margin-bottom: 8px;
  @media screen and (min-width: 768px) {
   min-width: 223px;
   margin-top: 8px;
  }
  @media screen and (min-width: 1360px) {
    min-width: 421px;
    margin-top: 8px;
   }
}

.grid-photo1{
  background-image: url(../img/Cases1-mobile.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 280px;
  min-height: 176px;
  margin: auto;
  position: relative;
  @media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  background-image: url('../img/Cases1-mobile2x.jpg');
}
@media screen and (min-width: 768px) {
  background-image: url(../img/Cases1-laptop.jpg);
  width: 223px;
  min-height: 148px;
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url('../img/Cases1-laptop2x.jpg');
  }
}
@media screen and (min-width: 1360px) {
  background-image: url(../img/Cases1-desktop.jpg);
  width: 421px;
  min-height: 282px;
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url('../img/Cases1-desktop2x.jpg');
  }
}
};

